import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Autoplay, Navigation } from "swiper";
import { Link } from "react-router-dom";
import { stripHtml } from "string-strip-html";
import { useContext } from 'react';
import { MainContext } from "../../contexts/MainContext";
import { NumericFormat } from "react-number-format";
import { Tooltip } from "@mui/material";

const FindAProperty = ({ data }) => {

  const { setImages, addToFavorites, compare, toggleCompare } =
    useContext(MainContext);

  return (
    <section className="elementor-section elementor-top-section elementor-element elementor-element-6242cc14d278c elementor-section-full_width elementor-section-stretched elementor-section-height-default elementor-section-height-default">
      <div className="elementor-container elementor-column-gap-default">
        <div className="elementor-column elementor-col-100 elementor-top-column elementor-element elementor-element-eead392">
          <div className="elementor-widget-wrap elementor-element-populated">
            <div
              className="elementor-element elementor-element-d90bc31 elementor-widget elementor-widget-beyot-property"
              data-element_type="widget"
              data-widget_type="beyot-property.default"
            >
              <div className="elementor-widget-container">
                <div className="ube-property">
                  <div className="ere-property-wrap">
                    <div
                      className="ere-property clearfix property-carousel  owl-move-nav-par-with-heading"
                      data-view-all-link="#"
                    >
                      <div className="container">
                        <div className="ere-heading ere-item-wrap heading-contain-owl-nav mt-16">
                          <h2>FIND A PROPERTY</h2>
                          <div className="owl-nav has-view-all owl-nav-top-right">
                            <div className="owl-prev">
                              <i className="fa fa-angle-left"/>
                            </div>
                            <div className="owl-next">
                              <i className="fa fa-angle-right"/>
                            </div>
                            <div className="view-all-link">
                              <a
                                href="#"
                                className="btn btn-xs btn-dark btn-classic"
                              >
                                View All
                              </a>
                            </div>
                          </div>
                          <p>BROWSE OUR DREAM HOUSE</p>
                        </div>
                      </div>

                      {/* begin */}
                      <Swiper
                        spaceBetween={30}
                        slidesPerView={1}
                        breakpoints={{
                          640: {
                            slidesPerView: 2,
                            spaceBetween: 6,
                          },
                          1000: {
                            slidesPerView: 4,
                            spaceBetween: 0,
                          },
                          1450: {
                            slidesPerView: 6,
                            spaceBetween: 0,
                          },
                        }}
                        pagination={{ clickable: true }}
                        navigation={{
                          prevEl: ".owl-prev",
                          nextEl: ".owl-next",
                        }}
                        modules={[Navigation]}
                        className="property-content owl-carousel manual owl-nav-top-right owl-loaded owl-drag"
                        data-callback="owl_callback"
                      >
                        {data.map((e, i) => {
                          const {
                            status,
                            propertytype,
                            description,
                            images,
                            selectedImg,
                          } = e;

                          const parsedStatus = JSON.parse(status);

                          const parsedImages = JSON.parse(e.images);

                          const pSelectedImg =
                            parsedImages?.[selectedImg] || parsedImages[0];

                          console.log('pSelectedImg: ' + pSelectedImg );

                          const parsedPropertyType = JSON.parse(propertytype);

                          const parseDate =
                            (new Date(e.created_at).getTime() -
                              new Date().getTime()) /
                            1000 /
                            2_592_000;

                          let currDate = "Some time ago";

                          if (parseDate < 1) {
                            currDate = "Less than a month ago";
                          } else if (parseDate < 12) {
                            currDate = `${Math.floor(parseDate)} month(s) ago`;
                          } else {
                            currDate = `${Math.floor(
                              parseDate / 12
                            )} year(s) ago`;
                          }

                          const desc = stripHtml(description);

                          return (
                            <SwiperSlide key={i} className="item h-48">
                              <div className="ere-item-wrap property-item ere-property-featured">
                                <div className="property-inner">
                                  <div className="property-image">
                                    <img
                                      decoding="async"
                                      loading="lazy"
                                      width="340"
                                      height="150"
                                      src={pSelectedImg}
                                      alt={e.name}
                                      title={e.name}
                                      style={{ height: '150px', width:"340px", objectFit: "cover" }}  // for example, max-height set to 200 pixels
                                    />

                                    <div className="property-action block-center">
                                      <div className="block-center-inner">
                                        <Tooltip
                                          arrow
                                          title={`(${parsedImages.length}) Photo(s)`}
                                        >
                                          <div
                                            onClick={() =>
                                              setImages(parsedImages)
                                            }
                                            className="property-view-gallery-wrap"
                                            data-toggle="tooltip"
                                          >
                                            <a
                                              data-property-id="8381"
                                              href={"javascript:void(0)"}
                                              className="property-view-gallery"
                                            >
                                              <i className="fa fa-camera"/>
                                            </a>
                                          </div>
                                        </Tooltip>

                                        <Tooltip arrow title="Add to Favorites">
                                          <a
                                            href={"javascript:void(0)"}
                                            className="property-favorite"
                                            data-property-id="8381"
                                            data-toggle="tooltip"
                                            data-title-not-favorite="Add to Favorite"
                                            data-title-favorited="It is my favorite"
                                            data-icon-not-favorite="fa fa-star-o"
                                            data-icon-favorited="fa fa-star"
                                            data-original-title="Add to Favorite"
                                            onClick={() => addToFavorites(e.id)}
                                          >
                                            <i className="fa fa-star-o"/>
                                          </a>
                                        </Tooltip>

                                        <Tooltip arrow title="Compare">
                                          <a
                                            className="compare-property"
                                            href={"javascript:void(0)"}
                                            onClick={() =>
                                              toggleCompare({
                                                id: e.id,
                                                image: pSelectedImg,
                                              })
                                            }
                                            data-property-id="8381"
                                            data-toggle="tooltip"
                                            data-original-title="Compare"
                                          >
                                            <i className={`fa ${compare.map(ef => ef.id).includes(e.id) ? 'fa-minus' : 'fa-plus'}`}/>
                                          </a>
                                        </Tooltip>
                                      </div>
                                      <Link
                                        className="property-link"
                                        to={`/property/${e.id}`}
                                        title={e.name}
                                      />
                                    </div>
                                    <div className="property-label property-featured">
                                      <p className="label-item">
                                        <span className="property-label-bg">
                                          Featured{" "}
                                          <span className="property-arrow"/>
                                        </span>
                                      </p>
                                    </div>
                                    <div className="property-status">
                                      <p className="status-item">
                                        <span
                                          className="property-status-bg"
                                          style={{ backgroundColor: "#dd3333" }}
                                        >
                                          {parsedStatus[0]?.label}
                                          <span
                                            className="property-arrow"
                                            style={{
                                              borderLeftColor: "#dd3333",
                                              borderRightColor: "#dd3333",
                                            }}
                                          />
                                        </span>
                                      </p>
                                    </div>
                                  </div>
                                  <div className="property-item-content">
                                    <div className="property-heading">
                                      <h2 className="property-title">
                                        <Link
                                          to={`/property/${e.id}`}
                                          title={e.title}
                                        >
                                          {e.name}
                                        </Link>
                                      </h2>
                                      <div className="property-price">
                                        <span>
                                          {e.callforprice == "true" ? (
                                            "Call for price"
                                          ) : (
                                            <NumericFormat
                                              thousandSeparator={true}
                                              displayType="text"
                                              value={Number(e.price)}
                                              prefix={e.beforepricelabel}
                                            />
                                          )}
                                        </span>
                                      </div>
                                    </div>
                                    <div
                                      className="property-location"
                                      title="SYRYS INVESTMENT LTD"
                                    >
                                      <i className="fa fa-map-marker" style={{stroke: 2}}/>
                                      <a
                                        target="_blank"
                                        href="http://maps.google.com/?q=SYRYS%20INVESTMENT%20LTD"
                                      >
                                        <span>SYRYS INVESTMENT LTD</span>
                                      </a>
                                    </div>
                                    <div className="property-element-inline">
                                      <div className="property-type-list">
                                        <i className="fa fa-tag" style={{stroke: 2}}/>
                                        <a
                                          href={`https://www.qsyrys.com/property-type/${parsedPropertyType[0]?.value}`}
                                          title={parsedPropertyType[0]?.label}
                                        >
                                          <span>
                                            {parsedPropertyType[0]?.label}
                                          </span>
                                        </a>
                                      </div>
                                      <div className="property-agent">
                                        <a href="#" title="SYRYS GROUP">
                                          {" "}
                                          <i className="fa fa-user"/>
                                          <span>SYRYS GROUP</span>
                                        </a>{" "}
                                      </div>
                                      <div className="property-date">
                                        <i className="fa fa-calendar"/>
                                        {currDate}
                                      </div>
                                    </div>
                                    <div className="property-excerpt">
                                      <p>{desc.result.substring(0, 660)} […]</p>
                                    </div>
                                    <div className="property-info">
                                      <div className="property-info-inner">
                                        <div className="property-area">
                                          <div
                                            className="property-area-inner property-info-item-tooltip"
                                            data-toggle="tooltip"
                                            title=""
                                            data-original-title="Size"
                                          >
                                            <span className="fa fa-arrows"/>
                                            <span className="property-info-value">
                                              {e.area} m<sup>2</sup>{" "}
                                            </span>
                                          </div>
                                        </div>
                                        <div className="property-bedrooms">
                                          <div
                                            className="property-bedrooms-inner property-info-item-tooltip"
                                            data-toggle="tooltip"
                                            title=""
                                            data-original-title={`${e.bedrooms} Bedroom`}
                                          >
                                            <span className="fa fa-hotel"/>
                                            <span className="property-info-value">
                                              {e.bedrooms}
                                            </span>
                                          </div>
                                        </div>
                                        <div className="property-bathrooms">
                                          <div
                                            className="property-bathrooms-inner property-info-item-tooltip"
                                            data-toggle="tooltip"
                                            title=""
                                            data-original-title={`${e.bathrooms} Bathroom`}
                                          >
                                            <span className="fa fa-bath"/>
                                            <span className="property-info-value">
                                              {e.bathrooms}
                                            </span>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </SwiperSlide>
                          );
                        })}
                      </Swiper>

                      {/* end */}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default FindAProperty;
